import { Component, OnInit,  EventEmitter, Output, ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NzMessageService } from 'ng-zorro-antd/message';

import { SiteService } from '../../services/site.service';
import {Site}  from '../../models/site';

import { AccountService } from '../../services/account.service';
import {Account}  from '../../models/account';

import {SiteFromComponent} from '../site-from/site-from.component'

@Component({
  selector: 'app-account-from',
  templateUrl: './account-from.component.html',
  styleUrls: ['./account-from.component.css']
})
export class AccountFromComponent implements OnInit {

  @Output() addEvent = new EventEmitter<boolean>();


  @ViewChild(SiteFromComponent)
  private siteFrom: SiteFromComponent;

  validateForm!: FormGroup;
  showAccountForm = false;
  sites:Array<Site>

  constructor(private fb: FormBuilder,
    private siteService:SiteService,
    private accountService:AccountService,
    private message: NzMessageService) { }

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      account: [null, [Validators.required]],
      siteId: [null, [Validators.required]],
    });
    this.fetchSites()
  }

  fetchSites(){
    this.siteService.sites().subscribe((data:Array<Site>) =>{
      this.sites = data
    })
  }

  show(){
  	this.showAccountForm = true
  }

  hidden(){
  	this.showAccountForm = false
  }
  addAccount(){
  	this.accountService.add(this.validateForm.value.account, this.validateForm.value.siteId)
      .subscribe((account:Account) => {
        this.showAccountForm = false
        this.addEvent.emit()
      })
  }

  showSiteModal(){
    this.siteFrom.show()
  }
}
