import { Injectable } from '@angular/core';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, Subject } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { Token } from '../models/token';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private loginStatusSource = new Subject<boolean>();
  loginStatus$ = this.loginStatusSource.asObservable();

  token:Token = null;
  
  constructor(private http: HttpClient) { 
  	var tokenValue = localStorage.getItem('access_token')

  	if(tokenValue){
  		this.token = new Token
  		this.token.access_token = tokenValue
  		this.token.expires_in = 3600
  		this.token.token_type = localStorage.getItem('token_type')
  	}
  }

  loginStatusChange() {
    this.loginStatusSource.next(!!this.token);
  }

  getLoginStatus(){
    return !!this.token
  }

  setToken(token){
    this.token = token
    localStorage.setItem('access_token', token.access_token);
    localStorage.setItem('token_type', token.token_type);

    this.loginStatusChange()
  }

  getToken(){
  	return this.token
  }

  login(email, password){
  	return this.http.post('/api/auth/login', {
  		email:email,
  		password:password
  	});
  }

  user(){
    return this.http.get('/api/profile');
  }

  logout(){
    localStorage.removeItem('access_token');
    localStorage.removeItem('token_type');
    this.token = null
    this.loginStatusChange()
  }

}
