import { Component, OnInit,EventEmitter,Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NzMessageService } from 'ng-zorro-antd/message';

import { SiteService } from '../../services/site.service';
import {Site}  from '../../models/site';
@Component({
  selector: 'app-site-from',
  templateUrl: './site-from.component.html',
  styleUrls: ['./site-from.component.css']
})
export class SiteFromComponent implements OnInit {

  @Output() addSiteEvent = new EventEmitter<boolean>();

  validateForm!: FormGroup;

  showSiteForm = false;
  constructor(private fb: FormBuilder,
    private siteService:SiteService,
    private message: NzMessageService) { }

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      name: [null, [Validators.required]],
      host: [null, [Validators.required]],
      description: [null],
    });
  }

  show(){
  	this.showSiteForm = true
  }

  hidden(){
  	this.showSiteForm = false
  }
  addSite(){
  	this.siteService.create(this.validateForm.value).subscribe((site:Site) => {
      this.hidden()
      this.addSiteEvent.emit()
    })
  }
}
