import { Component, OnInit, ViewChild } from '@angular/core';

import {AccountService} from '../../services/account.service';

import {Site} from '../../models/site';
import {Account} from '../../models/account';

import {AccountFromComponent} from '../../components/account-from/account-from.component';
@Component({
  selector: 'app-manager',
  templateUrl: './manager.component.html',
  styleUrls: ['./manager.component.css']
})
export class ManagerComponent implements OnInit {

  @ViewChild(AccountFromComponent)
  private accountFrom: AccountFromComponent;
  
  constructor(private accountService:AccountService) { }

  sites:Site[] = []
  accounts: Account[] = []



  ngOnInit(): void {
    this.fetchAccount()
  }

  fetchAccount(){
    this.accountService.accounts().subscribe((data:Array<Site>)=>{
      this.sites = data
      this.accounts = this.getAccount(this.sites)
    })
  }

  getAccount(sites){
  	let accounts:Array<Account> = []
  	sites.forEach((site, key)=>{
  		site.accounts.forEach((account,key) => {
  			account.site = site

  			accounts.push(account)
  		})
  	})
  	return accounts
  }
  showAccountForm(){
    this.accountFrom.show()
  }

  addAccountSuccess(){
    this.fetchAccount()
  }

  cancel(): void {
  }

  delete(account:Account): void {
    console.log(account)
    this.accountService.delete(account.id).subscribe((data)=>{
      this.fetchAccount()
    })
  }  
}
