import { Component, OnInit, Input,  SimpleChange  } from '@angular/core';

import MD5 from 'md5';
import Clipboard from 'Clipboard';

import { NzMessageService } from 'ng-zorro-antd/message';

import {AccountService} from '../../services/account.service';
import {AuthService} from '../../services/auth.service';

import {Site} from '../../models/site';
import {Account} from '../../models/account';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css']
})
export class AccountComponent implements OnInit {
  logined: boolean;

  bit = [1,4,7,10,13,16,19,22,25,28,31]

  sites:Array<Site> = []
  accounts: Array<Account> = []

  siteId:number
  accountId:number

  account:string
  domain:string
  hash:string

  password:string

  showCopy = false
  constructor(private accountService:AccountService,
    private msg:NzMessageService,
  	private authService:AuthService) { 
    this.logined = authService.getLoginStatus()
    authService.loginStatus$.subscribe(logined => {
      this.logined = logined
      if(this.logined){
        this.fetchAccount()
      }
    })
  }

  ngOnInit(): void {
  	if(this.logined){
      this.fetchAccount()
  	}
  }

  fetchAccount(){
    this.accountService.accounts().subscribe((data:Array<Site>)=>{
      this.sites = data
    })
  }

  generate(){
  	var g = MD5(this.account + "@" + this.domain + ":" + this.hash),
  		t = '';

  	this.bit.forEach(function(i){
  		t += ( i > 15 ) ? g[i].toUpperCase() : g[i];
  	})

  	this.password = t;
  	if(!this.showCopy){

  		this.showCopy = true
  		new Clipboard('.copy').on('success', (e) =>  {
        this.msg.success('密码已复制到粘贴板')
      });
  	}
  }

  changeSite(id){

  	for(var i in this.sites){
  		let site = this.sites[i]
  		if (site.id == id) {
  			this.accounts = site.accounts
  			this.domain = site.host
  		}
  	}
  }

  changeAccount(id){

  	for(var i in this.accounts){
  		let account = this.accounts[i]
  		if (account.id == id) {
  			this.account = account.account
  		}
  	}
  }
}
