import { Injectable } from '@angular/core';
import {
HttpEvent, HttpInterceptor, HttpHandler, HttpRequest,HttpResponse,HttpErrorResponse
} from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { map, retry, catchError } from 'rxjs/operators';

import { Router } from '@angular/router';

import { NzMessageService } from 'ng-zorro-antd/message';
import { AuthService } from '../services/auth.service'

/** Pass untouched request through to the next request handler. */
@Injectable()
export class AuthInterceptor implements HttpInterceptor {

    guestRoute:Array<string> = [
        '/api/auth/login'
    ];

    constructor(private auth: AuthService,
        private msg: NzMessageService, 
        private router: Router) {}

    intercept(req: HttpRequest<any>, next: HttpHandler) {
        var authReq;
        if(this.shouldAuth(req)){

            const authToken = this.auth.getToken();
            if(!authToken){
                this.router.navigate(['/dashboard'])
                throw "no auth";
            }

            authReq = req.clone({
                headers: req.headers.set('Authorization', authToken.token_type + ' ' +authToken.access_token)
            });
        }else{
            authReq = req;
        }
        return next.handle(authReq).pipe(
          catchError((error: HttpErrorResponse) =>  {
            if (error.error instanceof ErrorEvent) {
              // A client-side or network error occurred. Handle it accordingly.
              console.error('An error occurred:', error.error.message);
            } else {
              // The backend returned an unsuccessful response code.
              // The response body may contain clues as to what went wrong,
              console.error(
                `Backend returned code ${error.status}, ` +
                `body was: ${error.error}`);

              if(error.status == 401){
                this.auth.logout()
                this.router.navigate(['/dashboard'])
              }else if(error.status == 500){
              }else if(error.status == 422){
              }
              this.msg.error(`${error.error.message}`);
            }
            // return an observable with a user-facing error message
            return throwError(
              'Something bad happened; please try again later.');
          })
        );

    }

    shouldAuth(req: HttpRequest<any>){
        for (var key in this.guestRoute) {
            if (this.guestRoute[key] == req.url){
                return false
            }
        }
        return true
    }

}